import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    redirect: '/homepage',  // 重定向到主页
    children:[
      { path: 'homepage', name: 'HomePage', meta: { name: '首页' }, component: () => import('../views/front/HomePage.vue') },
      { path: 'homehead', name: 'HomeHead', meta: { name: '头条' }, component: () => import('../views/front/HomeHead.vue') },
      { path: 'hometech', name: 'HomeTech', meta: { name: '技术' }, component: () => import('../views/front/HomeTech.vue') },
      { path: 'homephoto', name: 'HomePhoto', meta: { name: '图片' }, component: () => import('../views/front/HomePhoto.vue') },
      { path: 'homepolicy', name: 'HomePolicy', meta: { name: '政策' }, component: () => import('../views/front/HomePolicy.vue') },
      { path: 'homecelebrating', name: 'HomeCelebrating', meta: { name: '校庆' }, component: () => import('../views/front/HomeCelebrating.vue') },
    ]

  },
]

const router = new VueRouter({
  routes
})

export default router
